@font-face {
  font-family: "Quite Chocolatey";
  src: url(../fonts/QuiteChocolatey.ttf);
}

@font-face {
  font-family: "Montserrat Regular";
  src: url(../fonts/Montserrat-Regular.ttf);
}

.header {
  width: 1200px;
  position: absolute;
}


// .header__container {
//   width: 1010px;
//   height: 102px;
//   margin: 0 auto;
//   position: relative;
// }
.header__content {
  width: 1010px;
  height: 102px;
  margin: 0 auto;
  position: relative;
  // flex-direction: row;
  // justify-content: space-between;
}

.header__logo {
  position: absolute;
  top: 40px;
  left: 21px;
  // margin-left: 17px;
  &-text {
    // padding-top: 25px;
    font-family: "Quite Chocolatey";
    font-size: 88px;
    line-height: 32px;
    color: #cf415d;
    text-decoration: none;
  }
}

.nav__container {
  position: absolute;
  top: 50px;
  left: 492px;
  width: 540px;
  // padding-top: 20px;
  &-item {
    display: inline-block;
  }
  &-list {
    display: inline-block;
    list-style-type: none;
    font-size: 0;
  }
  &-link {
    font-family: "Montserrat Regular";
    text-decoration: none;
    text-transform: uppercase;
    color: #1b1b1b;
    font-size: 18px;
    line-height: 22px;
    font-weight: 600;
    padding: 12px 15px;
  }
  &-link:hover {
    background-color: #d03958;
    color: #fff;
    border-radius: 5px/5px;
  }
}
