@font-face {
  font-family: "Montserrat Bold";
  src: url(../fonts/Montserrat-Bold.ttf);
}

@font-face {
  font-family: "PT Serif";
  src: url(../fonts/PTSerif-Regular.ttf);
}

@font-face {
  font-family: "Montserrat Regular";
  src: url(../fonts/Montserrat-Regular.ttf);
}

@font-face {
  font-family: "Couture Bold";
  src: url(../fonts/COUTURE-Bold.ttf);
}

.trevel__container {
  width: 100%;
  padding: 265px 0 163px 0;
  background-blend-mode: overlay;
  background-image: url(../img/travel_bg.png);
  background-color: #c7bda8;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 100% 70%;
}

.trevel__content {
  width: 1010px;
  display: flex;
  margin: 0 auto;
  flex-direction: row;
  justify-content: space-between;
  &-box {
    display: flex;
    margin-left: 16px;
    flex-direction: column;
  }
  &-head {
    font-family: "Montserrat Bold";
    font-size: 90px;
    line-height: 80px;
    text-transform: uppercase;
    color: #d03958;
  }
  &-text {
    width: 465px;
    // margin-top: 45px;
    font-family: "PT Serif";
    font-size: 20px;
    line-height: 28px;
    color: black;
    margin-top: 48px;
  }
}

.head__color {
  color: black;
}

.trevel__form-item {
  position: relative;
}

.trevel__form {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 34px 22px 30px 24px;
  background-color: #d03958;
  border-radius: 10px;
  position: absolute;
  right: -7px;
  // position: relative;
  // top: 150px
}

.trevel__form-registration {
  margin-top: 40px;
}

.trevel__form-head {
  font-family: "Montserrat Regular";
  font-size: 22px;
  text-transform: uppercase;
  color: #fff;
}

.travel__form-input {
  min-width: 353px;
  min-height: 57px;;
  font-family: "PT Serif";
  font-size: 20px;
  line-height: 25px;
  padding-left: 20px;
  border: none;
  border-radius: 5px;
}

.travel__form-input:not(:first-child) {
  margin-top: 20px;
}

.accept__terms-box {
  margin-top: 26px;
  &-checked {
    width: 18px;
    height: 18px;
    border: none;
    border-radius: 5px;
  }
  &-text {
    margin-left: 23px;
    font-family: "PT Serif";
    font-size: 20px;
    line-height: 25px;
    color: #fff;
  }
}

.travel__form-button {
  font-family: "Montserrat Regular";
  min-width: 353px;
  min-height: 57px;
  margin-top: 20px;
  background-color: #161616;
  border: none;
  border-radius: 5px;
  font-size: 22px;
  color: #fff;
  text-transform: uppercase;
  justify-content: center;
  &:hover {
    background-color: #942b3e;
    cursor: pointer;
  }
}

.our__services-container {
  width: 100%;
  padding: 40px 0 70px 0;
}

.our__services-content {
  width: 1010px;
  min-height: 380px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.our__services__box {
  width: 100%;
}

.our__services-box-head {
  font-family: "Couture Bold";
  font-size: 50px;
  line-height: 80px;
  &-color {
    color: #d03958;
  }
}

.our__services-topics-item {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.our__services-topics {
  display: flex;
  width: 180px;
  min-height: 247px;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  &-text {
    font-family: "PT Serif";
    font-size: 20px;
    line-height: 28px;
    color: #646464;
  }
}

.story__container {
  width: 100%;
  background-color: #d03958;
}
.story__content {
  width: 1010px;
  min-height: 120px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  &-text {
    width: 630px;
    font-family: "PT Serif";
    font-size: 20px;
    line-height: 28px;
    color: #fff;
  }
  &-button {
    font-family: "Montserrat Regular";
    font-size: 22px;
    line-height: 28px;
    color: #161616;
    text-transform: uppercase;
    background-color: #e3d5b8;
    padding: 20px 60px;
    border: none;
  }
}

.pricing__container {
  width: 100%;
  padding-top: 60px;
  background-color: #e3d5b8;
}

.pricing__content {
  width: 1010px;
  min-height: 480px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  &-head {
    font-family: "Montserrat Bold";
    font-size: 50px;
    line-height: 80px;
    text-transform: uppercase;
  }
}

.pricing__box {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.signup__plan {
  width: 320px;
  display: flex;
  background-color: #fff;
  border: none;
  border-radius: 5px;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  position: relative;
  &-name {
    padding-top: 35px;
    font-family: "PT Serif";
    font-size: 22px;
    line-height: 28px;
    color: #161616;
  }
  &-cost {
    font-family: "Montserrat Bold";
    font-size: 60px;
    line-height: 80px;
    color: #d03958;
    text-transform: uppercase;
  }
  &-info {
    font-family: "PT Serif";
    font-size: 20px;
    line-height: 28px;
    color: #161616;
  }
  &-button {
    font-family: "Montserrat Regular";
    font-size: 22px;
    line-height: 28px;
    color: #161616;
    text-transform: uppercase;
    padding: 10px 80px;
    background-color: #e3d5b8;
    border: none;
    border-radius: 5px;
    margin-top: 35px;
  }
  &-button:hover {
    cursor: pointer;
    color: #fff;
    background-color: #d03958;
  }
}

.story__content-button:hover {
  cursor: pointer;
  color: #fff;
  border: 1px solid;
}

.popular__triangle {
  font-family: "PT-Serif";
  font-size: 16px;
  position: absolute;
  top: 20px;
  left: 0;
  transform: rotate(-45deg);
}

.signup__plan:nth-child(2)::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background: linear-gradient(to left bottom, transparent 50%, #b4a993 0);
  width: 90px;
  height: 90px;
  transform: rotate(90deg);
}

.testi__gallery-container {
  width: 100%;
  background-color: #fff;
  padding: 140px 0 120px 0;
}

.testi__gallery-content {
  width: 1010px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.testi {
  width: 470px;
  display: flex;
  flex-direction: column;
  &__head {
    font-family: "Montserrat Bold";
    font-size: 50px;
    line-height: 80px;
    color: #1f1f1f;
    text-transform: uppercase;
    text-align: center;
  }
}

.testi__block {
  display: flex;
  flex-direction: column;
  width: 440px;
  padding: 20px 0 20px 20px;
  position: relative;
  // margin-bottom: 20px;
  &-text {
    width: 360px;
    font-family: "PT Serif";
    font-size: 20px;
    line-height: 28px;
    color: #161616;
  }
  &-client {
    font-family: "Montserrat Regular";
    font-size: 20px;
    line-height: 28px;
    color: #d03958;
    margin-top: 20px;
  }
  &:hover {
    background-color: #e3d5b8;
  }
}

.testi__block:not(:last-child) {
  margin-bottom: 20px;
}

.testi__block-icon {
  position: absolute;
  right: -50px;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: -4px -9px 15px rgba(219, 213, 201, 0.9);
}

.testi__block-icon-img {
  position: absolute;
  left: -70px;
  border: none;
}

.women__icon {
  left: -14px;
}

.gallery {
  width: 390px;
  display: flex;
  flex-direction: column;
  &__head {
    font-family: "Montserrat Bold";
    font-size: 50px;
    line-height: 80px;
    color: #d03958;
    text-transform: uppercase;
    text-align: center;
  }
  &__pictures {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 20px;
    margin-top: 30px;
  }
}

.gallery__pictures-icon {
  position: relative;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  overflow: hidden;
  box-shadow: -4px -9px 15px rgba(219, 213, 201, 0.9);
}

.gallery__pictures-icon-women {
  position: absolute;
  right: -35px;
  top: -5px;
  border: none;
}
.gallery__pictures-icon-berries {
  position: absolute;
  left: -60px;
  top: -50px;
  border: none;
}

.gallery__pictures-icon-lemon {
  position: absolute;
  left: -20px;
  top: -20px;
  border: none;
}

.gallery__pictures-icon-cup {
  position: absolute;
  right: -25px;
  bottom: 0;
  border: none;
}

.gallery__pictures-icon-legs {
  position: absolute;
  top: -15px;
  left: -10px;
  border: none;
}

.gallery__pictures-icon-rock {
  position: absolute;
  top: -140px;
  left: -200px;
  border: none;
}

.gallery__link {
  font-family: "Montserrat Regular";
  font-size: 20px;
  line-height: 28px;
  text-transform: uppercase;
  color: #d03958;
  text-align: center;
  margin-top: 46px;
}

.footer__container {
  width: 100%;
  padding: 80px 0 60px 0;
  background-color: #161616;
}

.footer {
  width: 1010px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  &__copyright {
    font-family: "PT Serif";
    font-size: 20px;
    line-height: 22px;
    color: #fff;
  }
}

.footer__copyright-color {
  color: #d03958;
}
